import { ref } from 'vue'

export default function () {
  const qrOptions = ref({
    width: 300,
    height: 300,
    qrOptions: {
      typeNumber: 0,
      mode: 'Byte',
      errorCorrectionLevel: 'H'
    },
    imageOptions: {
      hideBackgroundDots: true,
      imageSize: 0.4,
      margin: 0
    },
    dotsOptions: {
      type: 'dots',
      color: '#446bb3',
      gradient: null
    },
    //image: 'data:image/png;base64,',
    backgroundOptions: {
      color: '#FFFFFF',
      gradient: null
    },
    cornersSquareOptions: {
      type: 'dot',
      color: '#000000'
    },
    cornersDotOptions: {
      type: '',
      color: '#000000'
    }
  })
  return {
    qrOptions
  }
}
